import Modal from '@nextcloud/vue/dist/Components/Modal';
import { ActionButton, Actions, AppNavigationItem } from '@nextcloud/vue';
export default {
  name: 'NewLoyaltyCard',
  components: {
    AppNavigationItem: AppNavigationItem,
    ActionButton: ActionButton,
    Actions: Actions,
    Modal: Modal
  },
  directives: {},
  props: {
    modal: false
  },
  data: function data() {
    return {
      name: "",
      notes: "",
      store: "",
      value: ""
    };
  },
  computed: {},
  watch: {},
  mounted: function mounted() {},
  methods: {
    createCard: function createCard(e) {
      var loyaltyCard = {
        name: this.name,
        store: this.store,
        notes: this.notes,
        value: this.value
      };
      this.$emit('newLoyaltyCard', loyaltyCard);
    },
    cancelEdit: function cancelEdit(e) {
      this.clear();
    },
    clear: function clear() {
      this.name = "";
      this.notes = "";
      this.store = "";
      this.value = "";
    },
    closeModal: function closeModal() {
      this.clear();
      this.$emit('closeModal');
    },
    autoComplete: function autoComplete(search, callback) {// do nothing
    }
  }
};